
*{padding: 0;margin: 0;
}
html,body,#app{
  width: 100%;
  height: 100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  position: relative;
  z-index: 2;
}
canvas{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
}





































.envelope {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  width: 360px;
  height: 250px;
}
.triangle-down{
    border-left: 180px solid transparent;
    border-right: 180px solid transparent;
    border-top: 125px solid pink;
}
.triangle-up{
    border-left: 180px solid transparent;
    border-right: 180px solid transparent;
    border-bottom: 125px solid pink;
}
.heart{
  left: 156px;
  top: 99px;
}
.text{
  left: 44px;
  top: -100px;
}

.App{
  width: 80%;
  max-width: 800px;
  min-width: 400px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  margin-left: unset;
  margin-right: unset;
}
#autotype{
  margin-top: 40px;
}
